import * as React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import Box from '@material-ui/core/Box';
import List from '@material-ui/core/List';
import Grid from '@material-ui/core/Grid';
import Layout from 'gatsby-theme-aoi/src/layouts/JumbotronLayout';
import ListItemLink from 'gatsby-theme-aoi/src/components/ListItemLink';
import Jumbotron from '../components/Jumbotron';
import Stat from '../components/index/Stat';
import RadikoLink from '../components/index/RadikoLink';
import NavigationBox from '../components/NavigationBox';
import { useAllPrograms, useAllArtists } from '../utils/graphql-hooks';
import { ProgramIcon, ArtistIcon, CategoryIcon, SelectorIcon, CornerIcon, TuneIcon } from '../icons';
import { IndexQuery } from '../../graphql-types';

function IndexPage() {
  const programs = useAllPrograms();
  const tunesLength = programs.reduce((accum, curr) => [...accum, ...curr.playlist], []).length;
  const artistsLength = useAllArtists().length;

  const data = useStaticQuery<IndexQuery>(graphql`
    query Index {
      allProgram(sort: { fields: week, order: DESC }, limit: 8) {
        edges {
          node {
            id
            title
            week
            date(formatString: "YYYY-MM-DD")
            subtitle
            year
            categories
            guests
            fields {
              slug
            }
            playlist {
              artist
              corner
              id
              index
              indexInWeek
              kana
              label
              producer
              nation
              selector
              title
              week
              year
              youtube
            }
          }
        }
      }
    }
  `);
  const [latest] = data.allProgram.edges;
  const [firstSong] = data.allProgram.edges.reduce(
    (accum, { node }) => [...accum, ...node.playlist.filter((tune) => tune.youtube && tune.youtube !== '')],
    []
  );
  const jumbotron = (
    <Jumbotron
      title="SPITZ "
      header="TOKYO-FM 38"
      imgUrl={`https://i.ytimg.com/vi/${firstSong.youtube}/0.jpg`}
      height={346}
    />
  );

  return (
    <Layout maxWidth="md" componentViewports={{ BottomNav: false }} jumbotron={jumbotron}>
      <Grid container>
        <Stat icon={<ProgramIcon fontSize="inherit" />} value={programs.length} title="" label="" />
        <Stat icon={<TuneIcon fontSize="inherit" />} value={tunesLength} title="" label="" />
        <Stat icon={<ArtistIcon fontSize="inherit" />} value={artistsLength} title="" label="" />
      </Grid>
      <Typography variant="h5" component="h2">
        
      </Typography>
      <List>
        {data.allProgram.edges.map(({ node }, index) => (
          <ListItemLink
            key={index}
            to={node.fields.slug}
            primaryText={node.title}
            secondaryText={`${node.week} ${node.date}`}
            divider
          />
        ))}
      </List>
      <RadikoLink date={latest.node.date} />
      <Container maxWidth="sm">
        <Grid container>
          <Grid item sm={4} md={2}>
            <NavigationBox icon={<ProgramIcon />} label="" to="/programs/" />
          </Grid>
          <Grid item sm={4} md={2}>
            <NavigationBox icon={<ArtistIcon />} label="" to="/artists/" />
          </Grid>
          <Grid item sm={4} md={2}>
            <NavigationBox icon={<CategoryIcon />} label="" to="/categories/" />
          </Grid>
          <Grid item sm={4} md={2}>
            <NavigationBox icon={<SelectorIcon />} label="" to="/selectors/" />
          </Grid>
          <Grid item sm={4} md={2}>
            <NavigationBox icon={<CornerIcon />} label="" to="/corners/" />
          </Grid>
        </Grid>
      </Container>
    </Layout>
  );
}

export default IndexPage;
